<template>
  <v-app>
    <v-content>
      <div>
        <v-dialog v-model="addRoutine" transition="dialog-bottom-transition" :overlay=false scrollable>
          <v-card>
            <v-card-text>
              <v-select v-bind:items="days" v-model="selectedDay" label="Seleccione un dia" single-line bottom></v-select>
              <v-btn flat color="black" @click="addDay">
                <span>Agregar</span>
                <v-icon>save</v-icon>
              </v-btn>
            </v-card-text>

          </v-card>
        </v-dialog>
        <v-dialog v-model="addHour" transition="dialog-bottom-transition" :overlay=false scrollable>
          <v-card>
            <v-card-text>
              <v-flex md12 lg8>
                <v-menu lazy :close-on-content-click="false" v-model="menuFrom" transition="scale-transition" offset-y full-width :nudge-right="40"
                  max-width="290px" min-width="290px">
                  <v-text-field slot="activator" label="Desde" v-model="newHour.time.from" prepend-icon="access_time" readonly></v-text-field>
                  <v-time-picker v-model="newHour.time.from" :allowed-hours="allowedTimesNewFrom.hours"></v-time-picker>
                </v-menu>
              </v-flex>
              <v-flex md12 lg8>

                <v-menu lazy :close-on-content-click="false" v-model="menuTo" transition="scale-transition" offset-y full-width :nudge-right="40"
                  max-width="290px" min-width="290px">
                  <v-text-field slot="activator" label="Hasta" v-model="newHour.time.to" prepend-icon="access_time" readonly></v-text-field>
                  <v-time-picker :allowed-hours="allowedTimesNewTo.hours" :allowed-minutes="allowedTimesNewTo.minutes" v-model="newHour.time.to"></v-time-picker>
                </v-menu>
              </v-flex>
              <v-btn flat color="black" @click="addNewHour">
                <span>Agregar</span>
                <v-icon>save</v-icon>
              </v-btn>
            </v-card-text>

          </v-card>
        </v-dialog>
        <v-dialog v-model="modifyHour" v-if="modifyDay.ranges[modifiedHourIndex]" transition="dialog-bottom-transition" :overlay=false
          scrollable>
          <v-card>
            <v-card-text>
              <v-flex md12 lg8>
                <v-menu lazy :close-on-content-click="false" v-model="menuFromEdit" transition="scale-transition" offset-y full-width :nudge-right="40"
                  max-width="290px" min-width="290px">
                  <v-text-field slot="activator" label="Desde" v-model="modifyDay.ranges[modifiedHourIndex].from" prepend-icon="access_time"
                    readonly></v-text-field>
                  <v-time-picker v-model="modifyDay.ranges[modifiedHourIndex].from"></v-time-picker>
                </v-menu>
              </v-flex>
              <v-flex md12 lg8>

                <v-menu lazy :close-on-content-click="false" v-model="menuToEdit" transition="scale-transition" offset-y full-width :nudge-right="40"
                  max-width="290px" min-width="290px">
                  <v-text-field slot="activator" label="Hasta" v-model="modifyDay.ranges[modifiedHourIndex].to" prepend-icon="access_time"
                    readonly></v-text-field>
                  <v-time-picker v-model="modifyDay.ranges[modifiedHourIndex].to"></v-time-picker>
                </v-menu>
              </v-flex>
            </v-card-text>

          </v-card>
        </v-dialog>
      </div>

      <v-toolbar>
        <v-toolbar-title>Editor de horarios</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-side-icon class="hidden-md-and-up"></v-toolbar-side-icon>
        <v-toolbar-items class="hidden-sm-and-down">
          <v-btn flat color="black" @click="addRoutine = true">
            <v-icon>add</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-container fluid grid-list-sm>

        <v-layout row wrap>
          <v-flex xs6 v-for="routine, key in routine_data.routine">
            <v-card color="blue-grey darken-2" class="white--text">
              <v-card-title primary-title>
                {{days[routine.day].text}}
              </v-card-title>
              <v-list two-line subheader>
                <v-subheader>Horas</v-subheader>
                <v-list-tile avatar v-for="range, index in routine.ranges">
                  <v-list-tile-content>
                    <v-list-tile-title>{{days[routine.day].text}}</v-list-tile-title>
                    <v-list-tile-sub-title>{{range.from}} - {{range.to}}</v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-btn icon ripple @click="deleteRange(routine_data.routine[key], index)">
                      <v-icon color="grey lighten-1">delete</v-icon>
                    </v-btn>
                    <v-btn icon ripple @click="editHour(routine_data.routine[key], index)">
                      <v-icon color="grey lighten-1">create</v-icon>
                    </v-btn>
                  </v-list-tile-action>
                </v-list-tile>
              </v-list>
              <v-card-actions>
                <v-tooltip bottom>
                  <v-btn flat dark @click="openNewHour(key)" slot="activator">
                    <v-icon>add</v-icon>
                  </v-btn>
                  <span>Agregar nueva hora</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <v-btn flat dark @click="removeDay(routine.day)" slot="activator">
                    <v-icon>delete</v-icon>
                  </v-btn>
                  <span>Eliminar dia</span>
                </v-tooltip>
                <v-tooltip bottom>
                    <v-btn flat dark @click="propagateRoutine(routine)" slot="activator">
                      <v-icon>call_split</v-icon>
                    </v-btn>
                    <span>Propagar horario (Coloca todos los horarios como este)</span>
                  </v-tooltip>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>


</template>

<script>
  import axios from 'axios';
  export default {
    name: 'app',
    data() {
      return {
        addRoutine: false,
        addHour: false,
        menuFromEdit: false,
        menuToEdit: false,
        selectedHour: {},
        modifyDay: {
          ranges: [{
            from: '',
            to: ''
          }]
        },
        moment: require('moment'),
        modifyHour: false,
        modifiedHourIndex: 0,
        selectedDay: 0,
        menuFrom: false,
        menuTo: false,
        newHour: {
          time: {
            from: '12:30pm',
            to: '4:30pm'
          }
        },
        days: [{
            text: "Lunes",
            value: 0
          },
          {
            text: "Martes",
            value: 1
          }, {
            text: "Miercoles",
            value: 2
          }, {
            text: "Jueves",
            value: 3
          }, {
            text: "Viernes",
            value: 4
          }, {
            text: "Sabado",
            value: 5
          }, {
            text: "Domingo",
            value: 6
          },
        ],
        routine_data: {
          routine: []
        },
        allowedTimesNewTo: {
          hours: (hour) => {
            //var obj = new Date(this.newHour.time.from);
            var obj = this.moment(this.newHour.time.from, 'hh:mm a');
            return (obj.hours() < hour)

          },
          minutes: (minute) => {
            return (minute % 10 == 0);
          }
        },
        allowedTimesNewFrom: {
          hours: (hour) => {
            //var obj = new Date(this.newHour.time.from);
            var routine = this.routine_data.routine[this.selectedDay];
            var lastHour = routine.ranges[routine.ranges.length - 1];

            if (lastHour) {
              var lastHourObj = this.moment(lastHour.to, 'hh:mm a');
              return (lastHourObj.hours() < hour)
            }
            return true;
          },
          minutes: (minute) => {
            return (minute % 10 == 0);
          }
        }
      }
    },
    methods: {
      addDay() {
        if (this.routine_data.routine.filter(r => r.day == this.selectedDay).length != 0) return;
        this.routine_data.routine.push({
          day: this.selectedDay,
          ranges: []
        });
      },
      addNewHour() {
        var modifiedRoutine = this.routine_data.routine[this.selectedDay];
        modifiedRoutine.ranges.push({
          from: this.newHour.time.from,
          to: this.newHour.time.to
        });
        this.newHour.time.from = '12:30pm';
        this.newHour.time.to = '12:30pm';
        this.addHour = false;
      },
      removeDay(day) {
        this.routine_data.routine = this.routine_data.routine.filter(r => r.day != day);
      },
      deleteRange(day, index) {
        day.ranges = day.ranges.filter((range, i) => {
          console.log(range, i);
          return i != index;
        });

        this.modifyDay = {
          ranges: [{
            from: '0',
            to: '0'
          }]
        }
      },
      openNewHour(index) {
        this.selectedDay = index;
        this.addHour = true;
      },
      editHour(day, index) {
        console.log(day);
        this.modifyDay = day;
        this.modifiedHourIndex = index;
        this.modifyHour = true;

      },
      propagateRoutine(routine) {
        this.routine_data.routine.map((r) => {
          r.ranges = routine.ranges;
        });
      }
    }
  }

</script>

<style>
.icon:before {
  font-family: sans-serif;
}

</style>




